@import "../mixins";

.hero {
  padding: 256px 0 75px  0;

  &__container {
    @include flex-all-sb;
  }
}

.stock {
  max-width: 265px;
  max-height: 397px;
  border: 1px solid var(--silver-color);
  border-radius: 3px;

  &__top {
    margin: 0 0 3px 0;
    @include flex-all-sb;
    position: relative;
  }

  &__img {
    margin: 0 0 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__picture  {
    margin: 0 0 0 0;
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--base-color);
  }

  &__text {
    position: absolute;
    top: 0;
    left: 0;
    font-family: 'ProximaNova';
    padding: 8px 20px 8px 20px;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: var(--light-color);
    background-color: var(--accent-color);
  }

  &__price {
    padding: 4px 9px 0 0;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  &__price-actual {
    margin: 0 0 2px 0;
    font-family: 'Barlow';
    font-weight: 700;
    font-size: 27px;
    line-height: 118%;
    color: var(--accent-color);
  }

  &__price-crossed {
    margin: 0 0 0 0;
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    color: var(--silver-color);
  }

  &__name {
    margin: 0 0 7px 0;
    font-weight: 700;
    font-size: 18px;
    line-height: 116%;
    text-align: center;
    color: var(--text-color);
  }

  &__bottom-text {
    margin: 26px 0 2px 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 123%;
    color: var(--text-color);
  }

  &__bottom-date {
    margin: 0 0 25px 0;
    font-family: 'Barlow';
    font-weight: 700;
    font-size: 18px;
    line-height: 122%;
    color: var(--accent-color);
  }
}

.swiper-hero {
  margin: 0 0 0 0;
  max-width: 870px;
  max-height: 400px;

  &__next {
    color: var(--light-color);
  }

  &__prev {
    color: var(--light-color);
  }

  &-pagination-bullet{
    border: 2px solid var(--light-color);
  }

  &-pagination-bullet-active {
    background-color: var(--light-color);
  }
}

@include xl-laptop {
  .swiper-hero {
    max-width: 680px;
    max-height: 400px;
  }
}

@include lg-desktop {
  .swiper-hero {
    max-width: 100%;
  }
  .stock {
    display: none;
  }
}

.swiper-slide {

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide picture {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
