.banner {
  padding: 0 0 56px 0;

  &__ad {
    display: flex;
  }

  &__container {
    background-color: var(--accent-color);
  }
}

.ad-banner {

  &__img {
    margin: 33px 0 0 71px;
  }

  &__content {
    margin: 0 0 0 46px;
    max-width: 303px;
  }
  &__text {
    font-weight: 700;
    font-size: 36px;
    line-height: 100%;
    color: #FFFFFF;
  }

  &__action {
    margin: 54px 0 0 21px;
  }

  &__btn {
    padding: 4px 41px 4px 41px;
    font-weight: 500;
    font-size: 24px;
    line-height: 208%;
    text-align: right;
    color: #2F3035;
    background-color: var(--light-color);
    border: none;
  }
}
