.search {
  padding: 0 0 81px 0;

  &-top {
    margin: 0 0 25px 0;
  }

  &__link {
    margin: 0 28px 0 0;
    padding: 12px 26px 12px 26px;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--text-color);
    background-color: var(--light-color);
    border: none;
    cursor: pointer;
    transition: 0.5s ease-in-out;

    &-active {
      background-color: var(--base-color);
      border-radius: 5px;
    }
  }

  &__form {
    width: 100%;
    display: flex;
  }

  &__input {
    padding: 15px 0 15px 20px;
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    line-height: 119%;
    color: var(--dove-color);
    border: none;
    background-color: var(--base-color);
    border-radius: 3px;
  }

  &__button {
    padding: 17px 40px 17px 40px;
    font-weight: 700;
    font-size: 14px;
    line-height: 121%;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    color: var(--light-color);
    background-color: var(--accent-color);
    border: none;
    border-radius: 3px;
  }
}


