/* stylelint-disable */

@mixin xl-laptop {
  @media (max-width: (1170px)) {
    @content;
  }
}

@mixin lg-desktop {
  @media (max-width: (992px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
