input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.catalog {
  &__container {
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(12, 1fr);
  }

  &-filter {
    grid-column: 3 span;
    width: 100%;
  }

  &-grid {
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(9, 1fr);
    grid-column: 9 span;
  }
}

.grid {
 &-product {
  &__item {
    grid-column: 3 span;
  }
 }
}
.catalog-filter {

  &-opt {

    cursor: pointer;

    &--open {
      .catalog-filter__bottom {
        display: block;
      }

      .catalog-filter__title {
        &::before {
          background-image: url("../img/hide-arrow.png");
        }
      }
    }
  }

  &__bottom {
    display: none;
    padding: 0 0 24px 0;

    &--open {
      display: block;
    }
  }

  &__items {
    display: flex;
    align-items: center;
  }

  &__title {
    padding: 0 0 0 25px;
    position: relative;
    font-weight: 600;
    font-size: 16px;
    line-height: 119%;
    color: var(--text-color);

    &::before {
      position: absolute;
      top: 6px;
      left: 0;
      content: '';
      height: 7px;
      width: 12px;
      background-image: url("../img/dropdown-arrow.png");
    }
  }
}

.brand {
  &-items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  &-item {
    &:not(:last-child) {
      margin: 0 0 20px 0;
    }
  }
}

.grid-catalog {

  &__item {
    grid-column: 3 span;
  }
}

.filter {
  &-switch {
    margin: 0 0 42px 0;
  }

  &__list {
    padding: 0 0 0 0;
  }
  &__btn {
    padding: 0 0 7px 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 119%;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    color: var(--silver-color);

    &-active {
      background-color: var(--accent-color);
    }

    &:not(:last-child) {
      margin: 0 44px 0 0;
    }
  }
}
.custom-checkbox {
  margin: 0 0 0 0;
  display: inline-block;
  cursor: pointer;
  user-select: none;

  &__text {
    padding: 0 0 0 25px;
    position: relative;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    color: var(--text-color);

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 14px;
      height: 14px;
      border: 1px solid var(--silver-color);
      border-radius: 3px;
    }

    &::after {
      content: '';
      position: absolute;
      left: 1px;
      top: -1px;
    }
  }

  &__input {
    &:checked + span {
      color: var(--silver-color);

      &::before {
        border: 1px solid var(--accent-color);
        border-radius: 0px;
        background-color: var(--accent-color);
      }

      &::after {
        content: '\2713';
        position: absolute;
        left: 1px;
        top: -1px;
        color: #fff;
      }
    }
  }
}

.filters-price {
  padding: 0 0 44px 0;

  &__inputs {
    display: flex;
    justify-content: space-between;
  }
  &__label {
    display: flex;
    align-items: center;
  }

  &__input {
    padding: 0 5px 0 11px;
    font-family: 'Barlow';
    border: none;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    text-decoration-line: underline;
    color: var(--text-color);
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    color: var(--silver-color);
  }
}

.range-slider {

  &__input {
    height: 3px;
    display: block;
    width: 100%;
  }

  margin: 0 0 25px 0;
  position: relative;
  border-radius: 8px;
}

.characteristics {
  &__item {
    margin: 0 0 24px 0;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    border-bottom: 2px solid var(--silver-color);
  }

  &__text {
    margin: 0 0 15px 25px;
    font-weight: 600;
    font-size: 16px;
    line-height: 119%;
    color: var(--text-color);
  }
}

.dropdown {
  position: absolute;
  left: 0;
  top: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

  &--active {
    padding: 15px 0 8px 0;
    opacity: 1;
    visibility: visible;
    background-color: #fff;
    z-index: 2;
    border: 1px solid var(--silver-color);


    &::after {
      background-image: url("../img/hide-arrow.png");
    }

  }

  &__list {
    &-item {
      padding-left: 5px;
      width: 65px;
      display: flex;

      &:hover {
        background: rgba(107, 126, 172, 0.05);
      }


      &:not(:last-child) {
        margin: 0 0 3px 0;
      }
    }
  }

  &-item {
    position: relative;
  }

  &-btn {
    padding: 3px 28px 0 0;
    font-family: 'Barlow';
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    color: var(--abbey-color);
    border-radius: 3px;
  }

  &__link {
    font-family: 'Barlow';
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    color: rgba(0, 0, 0, 0.6);
    border-radius: 3px;
  }
}

.dropdown-btn--active {
  color: var(--accent-color);
}

.dropdown-btns {
  position: relative;

  &::after {
    position: absolute;
      top: 5px;
      left: 29px;
      content: '';
      height: 7px;
      width: 12px;
      background-image: url("../img/dropdown-arrow.png");
  }
}

.show-more {
  &__btn {
    margin: 18px 0 0 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    text-decoration-line: underline;
    color: var(--accent-color);
  }
}

.model {

  &-input {
    margin: 0 0 20px 0;
    padding: 8px 0 8px 20px;
    width: 100%;
    font-weight: 400;
    font-size: 12px;
    line-height: 117%;
    color: var(--french-color);
    border: 1px solid var(--alto-color);
  }

  &-item:nth-child(2) {
    margin: 0 0 20px 0;
  }

  &-item:nth-child(1) {
    margin: 0 0 20px 0;
  }
}

.stock {

  &__menu {
    display: flex;
  }

  &__item {
    &:not(:last-child) {
      margin: 0 10px 0 0;
    }
  }

  &-btn {
    padding: 6px 15px 4px 15px;
    font-weight: 700;
    font-size: 12px;
    line-height: 117%;
    letter-spacing: 0.07em;
    color: var(--silver-color);
    background-color: var(--base-color);

    &--accent {
      color: #fff;
      background-color: var(--accent-color);
    }

    &--dark {
      color: #fff;
      background-color: var(--text-color);
    }
  }
  &-item {
    &:not(:last-child) {
      margin: 0 30px 0 0;
    }
  }
}

.catalog {
  &-bottom {
    display: flex;
    flex-direction: column;
    align-items: center;


    &__btn {
      position: relative;
      margin: 0 0 15px 0;
      padding: 0 0 0 0;
      font-weight: 400;
      font-size: 13px;
      line-height: 123%;
      color: var(--text-color);

      &::after {
        content: '';
        position: absolute;
        top: 5px;
        right: -25px;
        width: 12px;
        height: 7px;
        background-image: url("../../img/dropdown-arrow.png");
      }
    }

    &__drop {
      font-weight: 400;
      font-size: 13px;
      line-height: 123%;
      text-decoration-line: underline;
      color: var(--silver-color);
    }
  }
}

.more {
  &-btn {
    margin: 0 0 15px 0;
    padding: 17px 85px 15px 85px;
    max-width: 252px;
    font-weight: 400;
    font-size: 15px;
    line-height: 120%;
    text-align: center;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    color: var(--french-color);
    background-color: var(--base-color);
  }
}

// product-grid styles =================================================================================================================

.grid {
  &-product {

    &__item {
      max-width: 271px;
    }

    &__card {
      position: relative;
      padding: 22px 10px 15px 11px;
      display: flex;
      flex-direction: column;
      border: 1px solid var(--silver-color);
      border-radius: 3px;
    }

    &__favorite {
      align-self: flex-end;
    }

    &__img {
      margin: 0 0 24px 0;
    }

    &__text {
      margin: 0 0 31px 0;
    }

    &__title {
      margin: 0 0 25px 0;
      font-weight: 400;
      font-size: 18px;
      line-height: 117%;
      text-align: center;
      color: var(--text-color);
    }

    &__price {
      margin: 0 0 0 0;
      font-family: 'Barlow';
      font-weight: 700;
      font-size: 30px;
      line-height: 120%;
      text-align: center;
      color: #2F3035;
    }

    &__cart {
      cursor: pointer;
      margin: 0 0 0 0;
      padding: 10px 16px 7px 14px;
      position: absolute;
      bottom: 0;
      right: 0;
      align-self: flex-end;
      background-color: var(--accent-color);
      border: none;
      border-radius: 10px 0px 0px 0px;
    }
  }
}



