.product-main {
  margin: 44px 0 82px 0;
}

.main {
  &-product {
    &__top {
      margin: 0 0 80px 0;
      width: 100%;
      display: inline-flex;
      justify-content: space-between;
    }
  }
}

.photos {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 500px;

  &__text {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-block;
    padding: 13px 25px 13px 26px;
    font-weight: 400;
    font-size: 15px;
    line-height: 120%;
    letter-spacing: 0.12em;
    color: #fff;
    background: var(--accent-color);
    border-radius: 3px;
  }

  &__img {
    margin: 64px 0 12px 0;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &-price {
    &__old {
      margin: 0 0 6px 0;
      font-family: 'Barlow';
      font-weight: 400;
      font-size: 30px;
      line-height: 120%;
      color: #000;
      opacity: 0.6;
    }

    &__new {
      margin: 0 11px 15px 0;
      font-family: 'Barlow';
      font-weight: 700;
      font-size: 40px;
      line-height: 120%;
      color: var(--text-color);
    }
  }

  &__links {
    display: flex;
  }

  &__link {
    font-weight: 400;
    font-size: 15px;
    line-height: 120%;
    color: var(--accent-color);

    &:not(:last-child) {
      margin: 0 13px 0 0;
    }
  }
}

.info {
  max-width: 570px;
  display: flex;
  flex-direction: column;

  &__text {
    margin: 0 0 21px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    margin: -8px 0 11px 0;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    color: var(--text-color);
  }

  &__code {
    margin:  0 0 0 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 120%;
    color: var(--text-color);
    opacity: 0.6;
  }

  &__rating {
    margin: 0 0 44px 0;
    display: flex;

    &-favorite {
      margin: 0 28px 0 0;
    }

    &-stats {
      margin: 0 25px 0 0;
    }
  }
}

.rating {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    &:not(:last-child) {
      margin: 0 7px 0 0;
    }
  }
}

.tabs {

  &__content {
    display: none;
    visibility: hidden;

    &--active {
      display: block;
      visibility: visible;
    }
  }

  &__list {
    display: flex;
  }

  &__item {
    &:not(:last-child) {
      margin: 0 32px 0 0;
    }
  }

  &__btn {
    padding: 0 0 4px 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    color: var(--text-color);

    &--active {
      border-bottom: 2px solid var(--accent-color);
      pointer-events: none;
    }
  }
}

.content-tabs {

  &__list {
    margin: 0 0 23px 0;
    display: flex;
    flex-direction: column;
    max-width: 436px;
  }

  &__item {
    margin: 0 0 1px 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(47, 48, 53, 0.1);
  }

  &__title {
    margin: 19px 0 0 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--text-color);
  }

  &__descr {
    margin: 19px 0 0 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--text-color);
  }

  &__show-more {
    margin: 0 0 44px 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    text-decoration-line: underline;
    color: var(--accent-color);
  }
}

.buy-btn {
  padding: 16px 57px 17px 55px;
  font-weight: 400;
  font-size: 14px;
  line-height: 121%;
  text-align: center;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: var(--accent-color);
  border-radius: 3px;
}

// service styles ===============================================================================================================================

.service {
  width: 100%;

  &__tabs {
    &-list {
      margin: 0 0 50px 0;
      padding: 22px 0 17px 0;
      display: flex;
      align-items: center;
      background-color: var(--base-color);
    }

    &-item {
      margin: 0 41px 0 38px;
    }
  }

  &__btn {
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--text-color);
    opacity: 0.6;
    border-bottom: 2px solid transparent;

    &--active {
      border-bottom: 2px solid var(--accent-color);
      pointer-events: none;
    }
  }

  &__content {
    display: none;
    visibility: hidden;

    &--active {
      display: block;
      visibility: visible;
    }
  }

  &-checkbox {
    margin: 0 12px 0 0;
  }
}

.content {
  &-service {
    margin-left: auto;
    max-width: 1134px;

    &__form {
      margin: 0 0 31px 0;
      display: flex;
      align-items: center;

      &-right {
        display: flex;
      }
    }

    &__label {
      font-weight: 400;
      font-size: 17px;
      line-height: 118%;
      color: #2F3035;
      opacity: 0.7;

      &:first-child {
        margin: 0 14px 0 0;
      }
    }

    &__input {
      position: relative;
      width: 313px;
      margin: 0 156px 0 0;
      padding: 8px 12px 8px 17px;
      background-color: var(--base-color);
      border: none;
      border-radius: 3px;

      &::after {
        position: absolute;
        top: 6px;
        right: 0;
        content: '';
        height: 16px;
        width: 16px;
        background-image: url("../../img/magnifier.svg");
        z-index: 100;
      }
    }

    &__headings {
      display: flex;
    }

    &__checkbox {
      &:not(:last-child) {
        margin: 0 53px 0 0;
      }
    }
  }
}

.headings {
  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: var(--text-color);

    &:nth-child(1) {
      margin: 19px 244px 0 0;
    }

    &:nth-child(2) {
      margin: 19px 123px 0 0;
    }

    &:nth-child(3) {
      margin: 19px 118px 0 0;
    }
  }
}

.list-service {
  display: flex;
  flex-direction: column;

  &__item {
    padding: 0 0 22px 0;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    border-bottom: 1px solid rgba(47, 48, 53, 0.1);
  }

  &__address {
    display: inline-flex;
    width: 314px;
    margin: 18px 0 0 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: var(--text-color);
    opacity: 0.7;
  }

  &__schedule {
    justify-self: flex-start;
    margin: 19px 0 0 0;
    display: flex;
    flex-direction: column;
  }

  &__availability {
    display: inline-flex;
    width: 204px;
    padding: 0 0 0 12px;
    font-weight: 400;
    font-size: 16px;
    line-height: 119%;
    color: var(--text-color);
    opacity: 0.7;
  }

  &__quantity {
    width: 236px;
    margin: 20px 0 0 0;
    padding: 0 0 0 21px;
    font-weight: 400;
    font-size: 16px;
    line-height: 119%;
    color: var(--text-color);
    opacity: 0.7;
  }

  &__btn {
    align-self: center;
    padding: 10px 42px 10px 40px;
    margin: 12px 0 0 32px;
    font-size: 13px;
    line-height: 123%;
    letter-spacing: 0.12em;
    color: #fff;
  }
}

.schedule {
  display: inline-flex;
  width: 253px;

  &__text {
    margin: 0 0 0 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 119%;
    color: var(--text-color);
    opacity: 0.7;

    &:not(:last-child) {
      margin: 0 0 10px 0;
    }
  }
}


