.breadcrumbs {

  padding: 221px 0 0 0;

  &__list {
    display: flex;
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin: 0 13px 0 0;

      &::after {
        content: '>';
        top: 0;
        left: 0;
        color: var(--silver-color);
        transform: translateX();
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 15px;
    line-height: 120%;
    color: var(--silver-color);
  }
}
