.categories {
  padding: 0 0 80px 0;

  &__container {
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(3, 1fr);
  }

  &__item {
    padding: 20px 20px 20px 20px;
    @include flex-all-sb;
    max-width: 370px;
    max-height: 129px;
    border: 1px solid var(--silver-color);
    border-radius: 3px;
  }

  &__left {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    margin: 0 0 0 0;
    font-family: 'Barlow';
    font-weight: 700;
    font-size: 23px;
    line-height: 121%;
    color: var(--text-color);
  }

  &__link {
    margin: 0 0 -6px 0;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    color: var(--silver-color);
  }

  &__img-last {
    margin: 0 58px 0 0;
  }
}
