@import "../mixins";

.popular {
  padding: 0 0 84px 0px;

  &__top {
    margin: 0 0 36px 0;
  }

  &__title {
    margin: 0 0 24px 0;
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 120%;
    color: var(--text-color);
  }

  &__menu {
    @include flex;
  }

  &-swiper__next {
    position: absolute;
    top: 202px;
    right: -44px;
    color: var(--text-color);
  }

  &-swiper__prev {
    position: absolute;
    top: 202px;
    left: -44px;
    color: var(--text-color);
  }
}

.popular__bottom {
  position: relative;
}

.content-popular {
  width: 1174px;
  position: relative;
}

@include xl-laptop {
  .content-popular {
    max-width: 1037px;
  }
}

@include lg-desktop {
  .content-popular {
    max-width: 874px;
  }
}

.menu-popular {
  &__item:not(:last-child) {
    margin: 0 50px 0 0;
  }

  &__link {
    margin: 0 0 0 0;
    font-weight: 500;
    font-size: 15px;
    line-height: 120%;
    color: #2F3035;
  }
}

.swiper-slide__popular {
  max-width: 271px;

  &:not(:last-child) {
    margin: 0 30px 0 0;
  }
}

@include lg-desktop {
  .swiper-wrapper {
  }
}

.item-popular {
  position: relative;
  padding: 16px 19px 6px 19px;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--silver-color);
  border-radius: 3px;

  &__favorite {
    margin: 0 0 0 0;
    align-self: flex-end;
  }

  &__img {
    margin: 9px 0 12px 17px;
    max-width: 200px;
    max-height: 200px;
  }

  &__title {
    margin: 0 0 27px 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 117%;
    text-align: center;
    color: var(--text-color);
  }

  &__price {
    justify-self: flex-end;
    margin: 0 0 23px 0;
    font-family: 'Barlow';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    text-align: center;
    color: var(--text-color);
  }

  &__cart {
    cursor: pointer;
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 10px 16px 7px 14px;
    background-color: #1C62CD;
    border-radius: 10px 0px 0px 0px;
    border: none;
    margin: 0 0 0 0;
    align-self: flex-end;
  }
}

.popular__more {
  margin: 34px 0 0 0;
  display: flex;
  justify-content: center;

  &-btn {
    padding: 17px 60px 17px 60px;
    font-weight: 400;
    font-size: 15px;
    line-height: 120%;
    text-align: center;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    color: var(--text-color);
    background-color: var(--base-color);
    border: none;
    cursor: pointer;
  }
}

.product__btn:disabled {
  opacity: 0.4;
  pointer-events: none;
}
