.footer {
  background-color: var(--base-color);

  &__top {
    padding: 43px 0 46px 0;
    display: flex;
  }

  &__form {
    max-width: 295px;
  }

  &__links {
    margin: 0 100px 0 161px;
    display: flex;
  }

  &__text {
    font-weight: 700;
    font-size: 14px;
    line-height: 121%;
    color: #2F3035;
  }

  &__bottom {
    padding: 0 0 22px 0;

    display: flex;
    justify-content: center;

    &-text {
      font-weight: 400;
      font-size: 10px;
      line-height: 120%;
      color: var(--abbey-color);
    }

    &-text:not(:last-child) {
      margin: 0 80px 0 89px;
    }
  }
}

.form-footer {

  &__input {
    padding: 8px 12px 8px 12px;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    color: var(--silver-color);
  }

  &__submit {
    padding: 10px 16px 10px 16px;
    font-weight: 600;
    font-size: 11px;
    line-height: 118%;
    text-align: center;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    color: #FFFFFF;
    background-color: var(--accent-color);
    border: none;
  }
}

.footer-nav {

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 121%;
    color: var(--text-color);
  }

  &__item {
    padding: 0 0 0 0;
  }

  &__item:not(:last-child) {
    margin: 0 0 5px 0;
  }

  &__link {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    color: var(--text-color);
  }
}

.footer-nav:not(:last-child) {
  margin: 0 100px 0 0;
}

.social {
  padding: 37px 0 0 0;

  &__item:not(:last-child) {
    margin: 0 30px 0 0;
  }

  &__list {
    display: flex;
  }
}

