@import "../mixins";

.burger {
	width: 33px;
	height: 27px;
	cursor: pointer;
	background-color: transparent;
	border: none;
	position: relative;
	display: none;
}

.burger--active {
  span {
    visibility: hidden;
  }
}

@include lg-desktop {
  .burger {
    display: block;
  }
}

.burger::before,
.burger::after {
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 2px;
	background-color: #000;
}

.burger--active::before {
  top: 24px;
  transform: rotate(45deg);
}

.burger--active::after {
  transform: rotate(-45deg);
}

.burger::after {
	top: auto;
	bottom: 0;
}

.burger span {
	position: absolute;
	left: 0;
	top: 50%;
	transform: translateY(-50%);
	width: 100%;
	height: 2px;
	display: inline-block;
	background-color: #000;
}



.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__top {
    padding: 25px 0 17px 0;
    @include flex-all-sb;
  }

  &__cart {
    margin: 7px 23px 0 0;
  }

  &__top-list {
    display: flex;
    align-items: center;
  }

  @include lg-desktop {
    .header-nav--active {
      display: none;
    }
  }

  @include lg-desktop {
    &__top-list {
      flex-direction: column;
      align-items: baseline;
    }
  }

  &__bottom-list {
    display: flex;
    align-items: center;
    justify-content: space-between
  }

  @include lg-desktop {
    .header__bottom-list {
      flex-direction: column;
      align-items: baseline;
    }
  }

  @include lg-desktop {
    .nav__item {
      width: 100%;
      border-bottom: 1px solid var(--text-color);

      &:not(:last-child) {
        margin: 0 0 0 0;
      }
    }
  }

  &__address {
    @include flex-all-sb;
  }

  @include lg-desktop {
    .nav__top {
      position: fixed;
      left: 0;
      top: 90px;
      max-width: 400px;
      width: 80%;
      z-index: 100;
      box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
      background-color: #fff;
    }
  }

  @include lg-desktop {
    .nav__bottom {
      position: fixed;
      left: 0;
      top: 255px;
      max-width: 400px;
      width: 80%;
      z-index: 100;
      box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
      background-color: #fff;
    }
  }

  @include lg-desktop {
    &__address {
      display: none;
    }
  }

  &__btns {
    position: relative;
    @include flex-all-sb;
  }

  &__bottom {
    padding: 0;
    background-color: var(--base-color);
  }
}

.favorite {
  margin: 0 10px 0 0;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  /* отображаем курсор в виде руки при наведении; некоторые
  считают, что необходимо оставлять стрелочный вид для кнопок */
  cursor: pointer;
}

.profile {
  margin: 0 10px 0 0;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  /* отображаем курсор в виде руки при наведении; некоторые
  считают, что необходимо оставлять стрелочный вид для кнопок */
  cursor: pointer;
}

.cart.active:hover {
  cursor: pointer;

}

.cart.active:hover .cart-content {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s, visibility 0.3s;
}

.cart {
  @include flex;
  position: relative;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  /* отображаем курсор в виде руки при наведении; некоторые
  считают, что необходимо оставлять стрелочный вид для кнопок */
  cursor: pointer;

  &-main {
    position: relative;
  };

  &__quantity {
    position: absolute;
    padding: 4px 0 0 0;
    top: 9px;
    left: 24px;
    min-width: 20px;
    min-height: 20px;
    font-weight: 500;
    font-size: 10px;
    line-height: 120%;
    text-align: center;
    color: #fff;
    background-color: var(--accent-color);
    border-radius: 100%;
  }

  &-content {
    cursor: default;
    position: absolute;
    top: 31px;
    right: -23px;
    width: 490px;
    background-color: #fff;
    box-shadow: 0px 4px 4px rgba(135, 135, 135, 0.25);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s, visibility 0.3s;

    &__list {
      max-height: 330px;
    }

    &__bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &__btn {
      margin: 24px 20px 24px 0;
      padding: 12px 53px 12px 53px;
      color: #fff;
      background-color: var(--accent-color);
    }
  }

  &-product {
    padding: 12px 20px 12px 20px;
    display: flex;
    align-items: center;
    transition: background-color 0.3s;

    &:hover {
      background-color: rgba(93, 113, 221, 0.1);
    }

    &__img {
      margin: 0 20px 0 0;
      max-width: 100px;
      max-height: 100px;
      object-fit: cover;
    }

    &__text {
      max-height: 100px;
    }

    &__title {
      margin: 0 0 17px 0;
      max-width: 280px;
      font-weight: 500;
      font-size: 14px;
      line-height: 143%;
      color: var(--text-color);
    }

    &__price {
      margin: 0 0 0 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 115%;
      color: var(--text-color);
    }

    &__delete {
      margin: 0 0 0 auto;
      border: none;
      background-color: transparent;
      cursor: pointer;
      background-image: url("../img/trash-bin.svg");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      flex-shrink: 0;
      width: 20px;
      height: 20px;
      padding: 0;
    }
  }
}

.simplebar-track.simplebar-vertical {
  width: 4px;
  margin: 20px 0 0 0;
  right: 5px;
  background-color: rgba(77, 77, 77, 0.1);
  border-radius: 5px;
  height: calc(100% - 40px);
}

.simplebar-track.simplebar-vertical .simplebar-scrollbar::before {
  background-color: rgba(77, 77, 77, 0.2);
  border-radius: 5px;
  width: 100%;
  opacity: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.fullprice {
  padding: 24px 0 24px 20px;
  display: flex;
  flex-direction: column;

  &__title {
    margin: 0 0 10px 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 143%;
    color: var(--text-color);
  }

  &__price {
    margin: 0 0 0 0;
    font-weight: 700;
    font-size: 20px;
    line-height: 115%;
    color: var(--text-color);
  }
}

.nav__link {
  font-weight: 700;
  font-size: 20px;
  line-height: 120%;
  text-align: center;
  color: var(--text-color);
}

.nav__item {
  padding: 15px 0 15px 0;
  border-bottom: 2px solid transparent;

  &-active {
    border-bottom: 2px solid var(--accent-color);
  }
}

.nav__item:not(:last-child) {
  margin: 0 57px 0 0;
}

@include xl-laptop {
  .nav__item:not(:last-child) {
    margin: 0 30px 0 0;
  }
}

.address__img {
  transform: translateY(-10px);
}

.address__text {
  font-weight: 700;
  font-size: 20px;
  line-height: 120%;
  color: var(--text-color);
}

.list-bottom__item:not(:last-child) {
  margin: 0 60px 0 0;
}

@include xl-laptop {
  .list-bottom__item:not(:last-child) {
    margin: 0 30px 0 0;
  }
}

.list-bottom__link {
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  text-transform: capitalize;
  color: var(--text-color);
}
