.header-catalog {

  padding-top: 41px;
  padding-bottom: 49px;

  &__title {
    margin: 0 0 19px 0;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    color: var(--text-color);
  }

  &__btn {
    padding: 4px 22px 4px 22px;
    font-weight: 400;
    font-size: 14px;
    line-height: 121%;
    color: var(--text-color);
    background-color: var(--base-color);
    border-radius: 3px;
    opacity: 0.7;

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__more {
    margin: 0 0 0 0;
    display: flex;
    justify-content: space-between;
    max-width: 271px;
  }
}


